import { Component, OnInit, HostListener, ViewChild, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { DocumentService } from 'src/app/api/controllers/Document';
import { SessionService } from 'src/app/core/session/session.service';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { MatDrawer } from '@angular/material/sidenav';
import { Router, ActivatedRoute } from '@angular/router';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-user-table-list',
  templateUrl: './user-table-list.component.html',
  styleUrls: ['./user-table-list.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class UserTableListComponent implements OnInit { 
  @ViewChild('matDrawer', {static: true}) matDrawer: MatDrawer; 
  drawerMode: 'over';

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  columnData:any;
  userListData:any;
  isUserDataLoaded:boolean = false;
  userSearch:string;
  totalItems:number;

  constructor(
    private documentService:DocumentService,
    private sessionService:SessionService,
    private changeDetectorRef: ChangeDetectorRef,
    private router: Router,
    private activeRouter: ActivatedRoute,
    private sharedService:SharedService,
  ) { }

  onBackdropClicked() {
    // Get the current activated route
    let route = this.activeRouter;
    while ( route.firstChild ) {
      route = route.firstChild;
    }
    // Go to the parent route
    this.router.navigate([`./list`], {relativeTo: this.activeRouter.parent});
    this.matDrawer.close();
    // Mark for check
    this.changeDetectorRef.markForCheck();
  }

  @HostListener('window:onUploadDocDrawer', ['$event.detail'])
  uploadDocument(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    let route = this.activeRouter;
    while ( route.firstChild ) {
      route = route.firstChild;
    }
    this.sharedService.setMatDrawerId(dataRecord.userID);
    this.router.navigate([this.router.url+'/', 'edit', dataRecord.userID], {relativeTo: route});
    this.changeDetectorRef.markForCheck();
  }


  searchData() {
    if(this.userSearch != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.userSearch;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'User Document');
  }

  ngOnInit() {
    let params = {
      ApartmentId: this.sessionService.apartmentId
    }
    this.documentService.getAllDocCountByApartmentId(params).subscribe((res:any) => { 
      this.isUserDataLoaded = true;
      if(res.length > 0) {
        this.totalItems = res.length;
        let userData= {
          localdata: res.reverse(),
          datatype: "array"
        }
        this.userListData = new jqx.dataAdapter(userData);
      }
    });

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.columnData = [{
      text: 'Name',
      datafield: 'firstName',
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },{
      text: 'Block No',
      datafield: 'apartmentBlockNumber',
      cellsrenderer: cellsrenderer,
      width: 300,
      renderer: columnrenderer
    },{
      text: 'Unit NO',
      datafield: 'apartmentBlockUnitNumber',
      cellsrenderer: cellsrenderer,
      width: 300,
      renderer: columnrenderer
    },{
      text: 'Documents',
      datafield: 'documents',
      cellsrenderer: cellsrenderer,
      width: 200,
      renderer: columnrenderer
    },{
      text: 'Actions',
      cellsalign: 'center', 
      align: 'center',
      cellsrenderer: (row: number): string => {
        return '<div class="simple-actions">'
        +'<a href="javascript:void(0)" onClick="uploadDocDrawer(' + row + ')"><i class="fa fa-eye icon edit" aria-hidden="true"></i></a>'
        +'</div>'
      },
      width: 100,
      renderer: columnrenderer
    }];
  }
}

function uploadDocDrawer(row){
  var event = new CustomEvent('onUploadDocDrawer', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).uploadDocDrawer = uploadDocDrawer;
