import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UserListsComponent } from './user-lists.component';
import { UserTableListComponent } from './user-table-list/user-table-list.component';
import { UserUploadDrawerComponent } from './user-upload-drawer/user-upload-drawer.component';
import { UserListResolverService } from './user-list-resolver.service';


const routes: Routes = [
  {
		path     : '', component: UserListsComponent,
		children : [
			{ path: '', redirectTo: 'list', pathMatch : 'full' },
			{ path: 'list', 
				component: UserTableListComponent,
				children : [
					{
						path         : ':type/:id',
						component    : UserUploadDrawerComponent,
						pathMatch: 'full',
						resolve: {
							data: UserListResolverService
						}
					}
				]
			}
		]
	}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UserListRoutingModule { }
